const EditorWrapper = experimentalStyled(Box)(
  ({ theme }) => `

    .ql-editor {
      min-height: 100px;
    }

    .ql-snow .ql-picker {
      color: ${theme.colors.alpha.black[100]};
    }

    .ql-snow .ql-stroke {
      stroke: ${theme.colors.alpha.black[100]};
    }

    .ql-toolbar.ql-snow {
      border-top-left-radius: ${theme.general.borderRadius};
      border-top-right-radius: ${theme.general.borderRadius};
    }

    .ql-toolbar.ql-snow,
    .ql-container.ql-snow {
      border-color: ${theme.colors.alpha.black[30]};
    }

    .ql-container.ql-snow {
      border-bottom-left-radius: ${theme.general.borderRadius};
      border-bottom-right-radius: ${theme.general.borderRadius};
    }

    &:hover {
      .ql-toolbar.ql-snow,
      .ql-container.ql-snow {
        border-color: ${theme.colors.alpha.black[50]};
      }
    }
`
);

const productTags = [
  { title: 'new' },
  { title: 'fresh' },
  { title: '2021' },
  { title: 'electronics' }
];

function GeneralSection(props) {
  const { t } = useTranslation();
  const { user } = useAuth();
  const [open, setOpen] = React.useState(false);

 

  const [tematicas,setTematicas] = useState(null)
  const [value, setValue] = useState('');
  const [selectedOptions, setSelectedOptions] = useState([]);
  const token = window.localStorage.getItem('accessToken');
 const [categorias,setCategorias] = useState([])
 const [categoriaId,setCategoriaId]  = useState('')
const [nombre,setNombre] = useState('')
  const [notificacion,setNotificacion] = useState({
    titulo: '',
    descripcion: '',
    descripcion_corta: '',
    categoria: ''
  })


  const { enqueueSnackbar } = useSnackbar();


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setNombre('')

  };

  const handle = (event, value) => setSelectedOptions(value);

  const llamarTematica = async () => {
    const result = await getTematica(token)
    setTematicas(result)
    console.log('tematica',result)
  }

  const llamadaCategorias = async (id) => {
    const result = await getCategorias(id,token)
    console.log('categorias',result)
    setCategorias(result)

}

  useEffect(()=>{

    llamarTematica()
    llamadaCategorias(user.clubId)

  },[])


  useEffect(()=>{

    setNotificacion({...notificacion,descripcion_corta:value})
    props.onchange(notificacion);

  },[value])
  
  useEffect(()=>{

    setNotificacion({...notificacion,categoria:categoriaId})
    props.onchange(notificacion);

  },[categoriaId])
  
  props.onchange(notificacion);

  console.log(notificacion)



const handleChange = e => {
    setNotificacion({
      ...notificacion,
      [e.target.name]: e.target.value
    });

  //  props.onchange(notificacion);
  };


  const agregarCategoria = async() => {
    handleClose()

    let obj = {
      nombre: nombre
    }
     await crearCategoria(obj,user.clubId,token)
     await llamadaCategorias(user.clubId)


     setNombre('')

      enqueueSnackbar(t('Se agrego la categoría de forma correcta.'), {
        variant: 'success',
        anchorOrigin: {
          vertical: 'top',
          horizontal: 'right'
        }
      });

  }

  return (
    <>
      <Grid item xs={8}>

       <Dialog open={open} 
       
       fullWidth={'sm'}
       maxWidth={'sm'}
       onClose={handleClose} >
        <DialogTitle>Agregar categoría</DialogTitle>
        <DialogContent xs={8}>
   
          <TextField
         
            autoFocus
            margin="dense"
            label="Nombre"
            type="text"
            fullWidth
            variant="outlined"
            onChange={(event)=>setNombre(event.target.value)}

                      />
        </DialogContent>
        <DialogActions>
          <Button variant="contained"  onClick={handleClose}>Cancel</Button>
          <Button variant="contained"  onClick={agregarCategoria} disabled={nombre === '' ? true : false}  >Agregar categoría</Button>
        </DialogActions>
      </Dialog>
      </Grid>
    <Card sx={{ p: 3 }}>
      
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <TextField
            fullWidth
            name="titulo"
            placeholder={t('Título...')}
            label="Título"
            variant="outlined"
            onChange={handleChange}
          />
        </Grid>

        <Grid item xs={12}>
          <TextField
            fullWidth
            name="descripcion"
            placeholder={t('Descripción...')}
            label="Descripción"
            variant="outlined"
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}
        
        >
          <EditorWrapper
    

          >
            <ReactQuill
          
          value={value} onChange={setValue}    
            />
          </EditorWrapper>
        </Grid>
      
          <Grid item xs={12}  boxShadow={3} >
          <FormControl variant="outlined"  style={{width: '100%'}}>
                      <InputLabel id="demo-simple-select-outlined-label">Categorías</InputLabel>
                      <Select
                        labelId="demo-simple-select-outlined-label"
                        id="demo-simple-select-outlined"

                        label="Categorías"
                        name="Categorias"
                        onChange={ event =>setCategoriaId(event.target.value)}

                      >
                        <MenuItem value="">
                          <em>Elegir</em>
                        </MenuItem>
                      
                        {categorias && categorias.map((val, i) => (
                          <MenuItem value={val.id} index={i}>{val.nombre}</MenuItem>
                        ))}

                      </Select>
                    </FormControl>
                      <Box mt={3}>
                      <Button  variant="contained" onClick={handleClickOpen}  >Agregar categorías</Button>


                      </Box>

          </Grid>

      </Grid>
    </Card>
    </>
  );
}

export default GeneralSection;
